import { sendPhoneCode, changePhone } from '@/api/index.js';
const wx = require('weixin-js-sdk');


export default {
    name: 'UserPhoneSet',
    data() {
        return {
            onflg:true,
            user_id:this.$store.state.user.userid,
            phone:'',
            codes:''
        };
    },
    beforeCreate() {

    },
    created(){
        
    },
    mounted() {
        console.log(this.userInfo)
        
    },
    methods:{
        sendPhoneCode(){
            if(this.phone=='') {
                this.feedback.Toast({  msg:'手机号不能为空',  timeout:1500 });
            }else if(this.phone.length!=11){
                this.feedback.Toast({  msg:'手机号长度有误，请重新输入',  timeout:1500 });
            }else if(this.onflg){
                sendPhoneCode({
                     name: 'bind_phone', 
                     user_id: this.user_id, 
                     mobile: this.phone 
                }).then(res => {
                    console.log('发送验证码',res)
                    if (res.data.code == 1) {
                        this.onflg = false
                        setTimeout(function () {
                            this.onflg = true 
                        }, 60000)
                    }
                });
            }
        },
        changePhone(){
            if(this.phone=='') {
                this.feedback.Toast({  msg:'手机号不能为空',  timeout:1500 });
            }else if(this.phone.length!=11){
                this.feedback.Toast({  msg:'手机号长度有误，请重新输入',  timeout:1500 });
            }else if(this.codes==''){
                this.feedback.Toast({  msg:'验证码不能为空',  timeout:1500 });
            }else{
                changePhone({
                     code: this.codes, 
                     user_id: this.user_id, 
                     phone: this.phone 
                }).then(res => {
                    console.log('修改手机号',res)
                    if (res.data.code == 1) {
                        this.feedback.Toast({  msg:'修改成功',  timeout:1500 });
                        setTimeout(()=>{
                             this.$router.back(-1)
                        },1500)
                    }else{
                        this.feedback.Toast({  msg:res.data.info,  timeout:1500 });
                    }
                });
            }
        }

    },
    components: {
    }
};




